.CoverPage {
  background-color:transparent;
  padding-right: 1%;
  padding-left: 1%; 
  padding-top: 10%
}

.CoverPage img {
  width: 21%;
  height: auto;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .CoverPage img:hover {
    animation: CoverPage-img-spin infinite 0.5s alternate;
  }
}

@keyframes CoverPage-img-spin {
  from {
    transform: rotate(-10deg);
  }
  to {
    transform: rotate(10deg);
  }
}





