.DrawerToggle {
  width: 30px;
  height: 80%;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-items: center;
  padding: 18px 0;
  padding-top: 5px;
  box-sizing: border-box;
  cursor: pointer;
}

.DrawerToggle div {
  width: 90%;
  height: 2px;
  background-color: black;
}

@media (min-width: 500px) {
  .DrawerToggle {
      display: none;
  }

}
