.NavigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%;
    background-color: transparent;
    font-size: 20px;
    padding-top: 5px;
}

@media (min-width: 500px) {
  .NavigationItems {
    flex-flow: row;
  }
}