.Logo {
  background-color: transparent;
  padding: 8px;
  height: 100%;
  box-sizing: border-box;
}


.Logo h3 {
  font-weight: 400;
  font-size: 24px;
  color: black;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
}

.Logo h3:hover {
    text-decoration: none;
    animation-name: shake;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    text-decoration-line: none;
    color: #FF0017;
}


@media (max-width: 500px) {
  .Logo {
    padding-top: 5px;
    padding-right: 0px;
  }

  .Logo h3 {
    font-size: 80%;
  }
}




